/**
* Back To Top Link
*
**/
#top-button {
    position: fixed;
    right: 20px;
    bottom: 20px;
    border: 1px solid #333;
    border-radius: 3px;
    display: none;
    padding: 10px;
    color: #333;
    background: #e7e7e7;
}

#top-button:hover {
    background: #ddd;
    color: #333;
}

body {
    background: #green;
}